@import '../../styles/mixin.scss';
@import '../../styles/variables.scss';
.chat{
    width: 100%;
    height: 100vh;
    background-color: $bg-elements;
    display: flex;
    flex-direction: column;
    overflow: hidden;
    @include tablet(){
        position: absolute;
        z-index: 1000;

    }
}
.not-found-chat{
    text-align: center;
    color:$color-text;
    fill: $color-text;
    width: 100%;
    padding-top: 20px;
    @include phone(){
        position: absolute;
        z-index: 1000;
        top: 0px;
        left: 0px;
        height: 100%;
        background-color: $bg-elements;
    }
}
.not-found-chat__arrow-left{
    width: 50px;
    height: 50px;
    & > svg {
        height: inherit;
        width: inherit;
    }
}